import React from 'react';
import Helmet from 'react-helmet';
import {Link, graphql} from 'gatsby';
import {Box, Anchor, Heading, Text} from 'grommet';
import styled from 'styled-components';
import _ from 'lodash';

import Section from '../components/Section';
import PageLayout from '../components/Layout';
import PrevNext from '../components/PrevNext';
import {DiscussionEmbed} from 'disqus-react';

const ContentBox = styled.div`
  img {
    max-width: 100%;
  }
`;

const StyledUL = styled.ul`
  list-style: none;
  padding-left: 1em;
  margin: 0px;
`;

const StyledLI = styled.li``;
const TreeNode = props => {
  let {post, children} = props;

  let aryChildren = [];

  _.forOwn(children, (value, key) => {
    aryChildren.push(value);
  });

  aryChildren = _.sortBy(aryChildren, o => {
    return o.post.fields.slug;
  });
  return (
    <div>
      {post && (
        <Link to={post.fields.slug}>
          <Text> {post.frontmatter.title}</Text>
        </Link>
      )}

      <StyledUL>
        {aryChildren &&
          aryChildren.map((child, idx) => (
            <StyledLI key={idx}>{TreeNode(child)}</StyledLI>
          ))}
      </StyledUL>
    </div>
  );
};

const BlogSeriesPost = ({
  pageContext: {prev, next, outline},
  data: {
    site: {siteMetadata: site},
    markdownRemark: post,
  },
}) => {
  let series = JSON.parse(outline);
  let localPath = post.fileAbsolutePath;
  let onlinePath =
    site.sourceUrl + localPath.substr(localPath.indexOf('content'));

  const disqusShortname = site.disqusShortname;
  const disqusConfig = {
    url: site.siteUrl + post.fields.slug,
    identifier: post.id,
    title: post.frontmatter.title,
  };
  return (
    <PageLayout>
      <Helmet
        htmlAttributes={{lang: 'en'}}
        meta={[{name: 'description', content: site.description}]}
        title={site.title}
      />
      <Box direction="row" justify="center" basis="auto">
        <Box basis="xlarge">
          <Box pad="small">
            <Heading textAlign="center" margin="small">
              {post.frontmatter.title}
            </Heading>
            <Box align="end">
              {post.frontmatter.date}{' '}
              <Anchor href={onlinePath} label="View page source" />
            </Box>
          </Box>
          <Box direction="row" pad="small">
            <ContentBox>
              <div dangerouslySetInnerHTML={{__html: post.html}} />
            </ContentBox>
          </Box>
        </Box>

        <Box
          basis="medium"
          pad="small"
          border={{color: 'lightgrey', side: 'left', size: 'small'}}>
          <Box align="center">
            <Heading level={3} margin="small">
              TOC
            </Heading>
          </Box>
          {TreeNode(series)}
        </Box>
      </Box>
      <PrevNext prev={prev} next={next} />
      <Disqus.DiscussionEmbed
        shortname={disqusShortname}
        config={disqusConfig}
      />{' '}
    </PageLayout>
  );
};

export default BlogSeriesPost;

export const pageQuery = graphql`
  query BlogSeriesPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
        sourceUrl
        disqusShortname
      }
    }
    markdownRemark(fields: {slug: {eq: $slug}}) {
      id
      excerpt
      html
      fileAbsolutePath
      fields {
        slug
        category
      }
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
      }
    }
  }
`;
